
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 97.5vh;
  
}

.main{
    width: 100% !important;
}

.name {
  width: auto;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.name-text {
    position: absolute;
    top: 30%;
    bottom: 0;
    right: 50%;
  text-align: left;
  margin-bottom: 30px;
}

.name-text h1 {
  font-size: 4.5rem;
  font-weight: 300;
  margin-bottom: 10px;
}

.name-text h2 {
    color: #e95757;
    margin-bottom: 10px;
    font-size: 3rem;
}

.name-text p {
  font-size: 1.6rem;
  line-height: 1.5;
}

.name-underline {
  width: 100%;
  height: 4px;
  background-color: #e95757;
  margin: 20px 0;
  border-radius: 10px;
}

.avt {
  position: absolute;
  margin-bottom: 50px;
  top: 25%;
  bottom: 0;
  left: 65%;
}

.Myavatar{
  width: 450px;
  height: 450px;
  border-radius: 50%;
}

.horizontal{
  position: absolute;
  bottom: 27%;
  left: 5%;
}

.horizontal p {
  font-size: 1.6rem;
  width: 1.6rem;
  height: auto;
  align-items: center;
  text-align: center;
  color: white;
  text-decoration: none !important;
  margin-left: 3.5px;
}

.seemore{
  padding: 20px 0;
  color: aliceblue;
  font-size: 2rem !important;
  align-items: center;
  /* background-color: aqua; */
}